import React from 'react';
import { connect } from 'react-redux';
import { t } from '../../../i18n';
import { DASH_NAMESPACE } from '../constants';
import ChartPanel from './chartPanel';
import OperationHistoryPanel from './operationHistoryPanel';
import {
  getAccounts,
  getSignaletic,
} from '../../../redux/accounts/accounts.actions';

class DashBoard extends React.Component {
  componentDidMount() {
    this.props.getAccounts();
  }
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.selectedAccountNumber && this.props.selectedAccountNumber)
      this.props.getSignaletic();
  }
  render() {
    const { fullName } = this.props.signaletic;
    return (
      <div className="page-content">
        <div className="page-head">
          <div className="page-head-compte">
            <div className="left-col">
              <div className="left__top col-bienvenue">
                <div className="profile_img_zone">
                  <img
                    src={require('../../../assets/img/logos/logo_anet.png')}
                    alt="loganet"
                  />
                </div>
                <h2 className="page-head-title">
                  {t(`${DASH_NAMESPACE}hello`)} {fullName}
                  <span>{t(`${DASH_NAMESPACE}welcome`)}</span>
                </h2>
              </div>
            </div>
          </div>
        </div>
        <div className="main-content container-fluid">
          <div className="row mx-auto">
            <div className="col-sm-12">
              <ChartPanel Accountsdata={this.props.data} />
            </div>
            <div className="col-sm-12">
              <OperationHistoryPanel Accountsdata={this.props.data} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const mapDispatchToProps = dispatch => ({
  getAccounts: () => {
    dispatch(getAccounts());
  },
  getSignaletic: () => {
    dispatch(getSignaletic());
  },
});
const mapStateToProps = state => ({
  signaletic: state.Accounts.signaletic,
  data: state.Accounts.chartData,
});
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(DashBoard);
