/**
 * @fileOverview Default Legend Content
 */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Surface from '../container/Surface';
import { LEGEND_TYPES } from '../util/ReactUtils';
import { COLOR_PANEL } from '../util/Constants';
import { t } from '../../../i18n';

const SIZE = 32;
const ICON_TYPES = LEGEND_TYPES.filter(type => type !== 'none');

class ChartLegendContent extends PureComponent {
  static displayName = 'Legend';

  static propTypes = {
    content: PropTypes.element,
    iconSize: PropTypes.number,
    iconType: PropTypes.oneOf(ICON_TYPES),
    layout: PropTypes.oneOf(['horizontal', 'vertical']),
    align: PropTypes.oneOf(['center', 'left', 'right']),
    verticalAlign: PropTypes.oneOf(['top', 'bottom', 'middle']),
    payload: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.any,
        id: PropTypes.any,
        type: PropTypes.oneOf(LEGEND_TYPES),
      }),
    ),
    inactiveColor: PropTypes.string,
    formatter: PropTypes.func,
    onMouseEnter: PropTypes.func,
    onMouseLeave: PropTypes.func,
    onClick: PropTypes.func,
  };

  static defaultProps = {
    iconSize: 14,
    layout: 'horizontal',
    align: 'center',
    verticalAlign: 'middle',
    inactiveColor: '#ccc',
  };

  /**
   * Render the path of icon
   * @param {Object} data Data of each legend item
   * @return {String} Path element
   */

  /**
   * Draw items of legend
   * @return {ReactElement} Items
   */
  renderItems() {
    const { iconSize } = this.props;
    const viewBox = { x: 0, y: 0, width: SIZE, height: SIZE };
    const svgStyle = {
      display: 'inline-block',
      verticalAlign: 'middle',
      marginRight: 4,
    };
    return (
      <tr>
        <td width="20">
          <div
            className="bg-colonne"
            style={{
              borderRight: `4px solid ${COLOR_PANEL[this.props.activeIndex]}`,
            }}
          ></div>
          <Surface
            width={iconSize}
            height={iconSize}
            viewBox={viewBox}
            style={svgStyle}
          >
            <path
              stroke="none"
              fill={COLOR_PANEL[this.props.activeIndex]}
              d={`M0,${SIZE / 8}h${SIZE}v${(SIZE * 3) / 4}h${-SIZE}z`}
              className="recharts-legend-icon"
            />
          </Surface>
        </td>
        <td>
          <span className="name">{this.props.activeItem.AccountName}</span>
          <span className="num">
            {t('Components:number')}
            {this.props.activeItem.AccountNumber}
          </span>
        </td>
        <td className="align_right">
          <span className="size16 uppercase_txt hard_grey_txt bold evolutionCompteSolde">
            {this.props.activeItem.Solde}({t('Components:currency')})
          </span>
        </td>
      </tr>
    );
  }

  render() {
    const finalStyle = {};

    return (
      <table style={finalStyle}>
        <tbody>{this.renderItems()}</tbody>
      </table>
    );
  }
}

export default withTranslation()(ChartLegendContent);
