import React from 'react';
import Panel from '../../../components/Panel/Panel';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import { getAllAccountsOperations } from '../../../redux/operations/operations.actions';
import FormatDate from '../../../components/FormattedDate/FormattedDate';
import FormattedNumber from '../../../components/FormattedNumber/FormattedNumber';
import FormatsConfiguration from '../../../utils/FormatsConfiguration';
import OperationTable from '../../../components/DataTable/CustomDataTable';
import DateCalender from '../../../components/DateCalendar/DateCalendar';
import { dateFilter } from '../../../utils/DateUtils';
import {
  exportTableToPdf,
  exportTableToExcel,
} from '../../../utils/exportUtils';
import { COLOR_PANEL } from '../../../components/Chart/util/Constants';
import { DASH_NAMESPACE } from '../constants';
import DashboardOperationsTemplateExcel from '../../../excel/Templates/DashboardOperationsTemplateExcel';
import DashboardOperationsTemplate from '../../../pdf/Templates/DashboardOperationsTemplate';
import { getSelectedAccountNumber } from '../../../redux/accounts/accounts.selectors';

class operationHistoryPanel extends React.PureComponent {
  constructor() {
    super();
    let maxDate = new Date();
    this.state = {
      maxDate: maxDate,
    };
  }

  _dateChangeHandler = () => {
    this.forceUpdate();
  };

  componentDidMount() {
    this.props.getAllAccountsOperations();
  }

  _buildDataTableRows = () =>
    this.props.operations.map(data => {
      const { date, description, amount, sign, clientAccountId } = data;
      const index = this.props.Accountsdata.findIndex(
        account => account.accountId === clientAccountId,
      );
      let signedAmount;
      if (sign === 'C') {
        signedAmount = amount;
      } else {
        signedAmount = -amount;
      }
      let accountName = '';
      let accountNumber = '';
      if (this.props.Accountsdata.length > 0) {
        accountName = this.props.Accountsdata[index].AccountName;
        accountNumber = this.props.Accountsdata[index].AccountNumber;
      }

      return {
        date: date,
        accountName: accountName,
        accountNumber: accountNumber,
        description: description,
        amount: signedAmount,
        sign: sign,
        accountId: clientAccountId,
      };
    });
  _dateTemplate = rowData => {
    const index = this.props.Accountsdata.findIndex(
      account => account.accountId === rowData.accountId,
    );

    return (
      <FormatDate
        dateTime={rowData.date}
        format={FormatsConfiguration.DateTimeFormat.fullDate}
        style={{
          alignItems: 'center',
          display: 'flex',
          background: COLOR_PANEL[index],
          height: '100%',
          position: 'absolute',
          width: '4px',
          left: '0',
          top: '0',
        }}
      />
    );
  };
  _accountTemplate = rowData => (
    <div>
      <span className="text-strong">{rowData.accountName}</span>
      <span className="text-light">
        {t(`${DASH_NAMESPACE}number`)}
        {rowData.accountNumber}
      </span>
    </div>
  );
  _amountTemplate = rowData => {
    if (rowData.sign === 'C') {
      return (
        <span style={{ color: 'green' }}>
          <FormattedNumber
            number={rowData.amount}
            positive
            format={FormatsConfiguration.AmountFormat}
          />
        </span>
      );
    } else {
      return (
        <span style={{ color: 'red' }}>
          <FormattedNumber
            number={rowData.amount}
            format={FormatsConfiguration.AmountFormat}
          />
        </span>
      );
    }
  };
  _dateCalendarComponent = maxDate => (
    <DateCalender
      maxDate={maxDate}
      handleDateChange={e => {
        this.dt.filter(e.value, 'date', 'custom');
        this._dateChangeHandler();
      }}
    />
  );
  _getPdfFileName = () =>
    `${t('Accounts:history')}_${this.props.selectedAccountNumber}.pdf`;
  _getSheetName = () =>
    `${t('Accounts:history')}_${this.props.selectedAccountNumber}`;

  render() {
    let rows = this._buildDataTableRows();
    return (
      <div className="page-container_inner">
        <div className="row mx-auto">
          <div
            className="col-sm-12"
            style={{ paddingLeft: 0, paddingRight: 0 }}
          >
            <DashboardOperationsTemplateExcel tableRows={rows} />
            <DashboardOperationsTemplate tableRows={rows} />
            <Panel
              title={t(`${DASH_NAMESPACE}operationHistory`)}
              hasDownloadLink
              pdfLinkAction={() =>
                exportTableToPdf(
                  'operationsSummary',
                  'operationsLogo',
                  'operationsData',
                  this._getPdfFileName(),
                )
              }
              csvLinkAction={() =>
                exportTableToExcel('Excel', this._getSheetName())
              }
              selectedAccountNumber={this.props.selectedAccount}
            >
              <OperationTable
                value={rows}
                onRowReorder={e => (rows = e.value)}
                reference={el => (this.dt = el)}
                columnResizeMode="fit"
                cols={[
                  {
                    bodyClassName: 'td-dateclass',
                    field: 'date',
                    header: t(`${DASH_NAMESPACE}date`),
                    body: this._dateTemplate,
                    filter: true,
                    filterElement: this._dateCalendarComponent(
                      this.state.maxDate,
                    ),
                    filterFunction: dateFilter,
                  },
                  {
                    field: 'accountName',
                    header: t(`${DASH_NAMESPACE}accountName`),
                    body: this._accountTemplate,
                  },
                  {
                    field: 'description',
                    header: t(`${DASH_NAMESPACE}label`),
                    className: 'small-col',
                  },
                  {
                    field: 'amount',
                    header: t(`${DASH_NAMESPACE}amount`, {
                      currency: t(`${DASH_NAMESPACE}currency`),
                    }),

                    body: this._amountTemplate,
                  },
                ]}
              />
            </Panel>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getAllAccountsOperations: accountId => {
    dispatch(getAllAccountsOperations(accountId));
  },
});

const mapStateToProps = state => ({
  operations: state.Operations.allAccountsOperations,
  selectedAccountNumber: getSelectedAccountNumber(state),
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(operationHistoryPanel),
);
