import React from 'react';
import { t } from '../../../i18n';
import { DASH_NAMESPACE } from '../constants';
import Chart from '../../../components/Chart/ProvChart';
import Panel from '../../../components/Panel/Panel';

const chartPanel = props => (
  <Panel title={t(`${DASH_NAMESPACE}balanceEvolution`)}>
    {props.Accountsdata.length > 0 ? (
      <Chart data={props.Accountsdata} />
    ) : (
      <div />
    )}
  </Panel>
);
export default chartPanel;
